<script>
import { computed, inject, reactive, watch } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const summaryForm = inject("summaryForm");
        const form = reactive({
            letSupport: false
        });

        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        watch(form, (newValue) => {
            summaryForm.letSupport = newValue.letSupport;
        });

        watch(monitorDetails, (newValue) => {
            form.letSupport = !!newValue.letSupport;
        });

        return {
            summaryForm
        };
    }
};
