            store,
            loading,
            form,
            save,
            fillForm,
            typeOfStimulationProtocolOptions,
            triggerMedicationOptions,
            medicationForCOSOptions,
            reasonCancelledOptions
        } = useOvariannFormInit();

        watch(() => store.state.born.ovarian, fillForm);

        onMounted(() => {
            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        return {
            form,
            save,
            loading,
            typeOfStimulationProtocolOptions,
            triggerMedicationOptions,
            medicationForCOSOptions,
            reasonCancelledOptions
        };
    }
